.skills-card {
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 4rem;
    text-align: center;
    background: rgba(22,17,47,0.398);
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;

}

.skills-card:hover,
.skills-card.active {
    background: linear-gradient(180deg,#aa94fe 0%,#7c5fe6 100%);
}

.skills-card span{
    font-size: 1.3rem;
    font-weight: 500;
}
.skills-icon{

    width: 5rem;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #2a2252;
    border-radius: 0.65rem;
    border: 1.5px solid #6852ba;
    position: absolute;
    top:-1rem;
    left: -1rem;
}

.skills-icon img {
    width: 3rem;
    height: auto;
    object-fit: contain;
}