.skills-container {
    margin: 4rem 0;
    position: relative;

}

.skills-container h5{
    font-size: 1.5rem;
    align-items: flex-start;
    gap: 3rem;
    padding-bottom: 4rem;
}

.skills-container::after,
.skills-container::before {
  content: "";
  width: 28rem;
  height: 28rem;
  border-radius: 28.125rem;
  background: #7c66e3;
  position: absolute;
  z-index: -1;
  filter: blur(200px);
}

.skills-container::after {
  top: -3rem;
  left: -5rem;
}

.skills-container::before {
  background: #9e51b9;
  bottom: 0rem;
  right: 0rem;
}

.skills-contenido {
    display: flex;
    align-items: flex-start;
    gap: 4rem;
}

.skills {
    flex:1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2,1fr);
}

.skills-info{
    flex: 1;
}